<template>
  <div>
    <el-table :data="userlist" height="410" border>
      <el-table-column prop="u_id" label="店主ID" />
      <el-table-column prop="u_nickname" label="店主" />
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button @click="show(scope.row)">商家销售情况</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog style="margin-top: 2vh;" title="商家销售情况统计" :visible.sync="dialogVisible" width="40%" :before-close="handleClose">
      <div>
        <el-table v-if="list" :data="list" height="410" border>
          <el-table-column prop="saleDate" label="时间" />
          <el-table-column prop="num" label="完成订单数" />
          <el-table-column prop="total" label="营业额" />
        </el-table>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="handleClose">取 消</el-button>
        <el-button type="primary" @click="handleClose">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import dateformat from "../../../config/dateformat"
export default {
  data() {
    return {
      id: 0,
      dialogVisible: false,
      list: [],
      userlist: []
    }
  },
  created() {
    this.getuserlist()
  },
  methods: {
    async getuserlist() {
      const { data: res } = await this.$http.get("showAllShopper")
      console.log(res)
      if (res.code !== 200) return
      this.userlist = res.obj
      console.log(this.userlist)
    },
    async getstatistics(id) {
      const { data: res } = await this.$http.get("goods/showSaleChart", { params: { id } })
      console.log(res)
      if (res.code !== 200) return
      this.list = res.obj
      this.list.some(item => {
        item.saleDate = dateformat.dateformat(item.saleDate)
      })
      console.log(this.list)
    },
    show(row) {
      this.dialogVisible = true
      this.getstatistics(row.u_id)
    },
    handleClose() {
      this.dialogVisible = false
    }
  }
}
</script>
