<template>
  <div class="menu">
    <el-menu background-color="#383636" text-color="#fff" :default-active="active" unique-opened :collapse="iscollapse" router>
      <div v-for="item in menu" :key="item.id">
        <el-menu-item :index="item.path">
          <div slot="title">
            <span slot="title">{{ item.item }}</span>
          </div>
        </el-menu-item>
      </div>
    </el-menu>
  </div>
</template>

<script>
export default {
  data() {
    return {
      active: "/user",
      iscollapse: false,
      user: JSON.parse(window.sessionStorage.user),
      menu: [
        { id: 1, path: "/mana/shopper", item: "商家" },
        { id: 2, path: "/mana/scenery", item: "风景" },
        { id: 3, path: "/mana/info", item: "资讯" },
        { id: 4, path: "/mana/statistics", item: "统计" }
      ]
    }
  },
  created() {},
  mounted() {
    // 根据当前地址改变菜单选中状态
    this.active = this.$route.path
  },
  methods: {},
  watch: {
    "$route.path": function(newVal, oldVal) {
      if (newVal.includes("mana")) {
        this.active = newVal
      }
    }
  }
}
</script>

<style lang="less" scoped>
.menu {
  height: 450px;
}
.el-menu {
  height: 100%;
  border-right: none;
  background-color: #bfedf79c;
  a {
    text-decoration: none;
  }
}
.iconfont {
  font-size: 30px;
  margin-right: 10px;
}
.glyphicon {
  top: 0;
  margin-right: 5px;
  width: 24px;
  text-align: center;
  font-size: 18px;
  vertical-align: middle;
}
</style>
