<template>
  <div>
    <div class="flex-between">
      <h3>差评过多商家</h3>
      <div>
        <span>差评数：</span>
        <el-input style="width:150px" v-model="num" type="number" min="0"></el-input>
      </div>
    </div>
    <el-table :data="shopperlist" height="410" border>
      <el-table-column prop="u_id" label="商家ID" />
      <el-table-column prop="u_nickname" label="商家名" />
      <el-table-column prop="u_tel" label="联系电话" />
      <el-table-column prop="u_email" label="邮箱" />
      <el-table-column prop="u_stuta" label="状态">
        <div slot-scope="scope">
          {{ scope.row.u_stuta === 1 ? "正常" : "禁用" }}
        </div>
      </el-table-column>
      <el-table-column label="操作">
        <div slot-scope="scope">
          <el-button title="修改" type="warning" size="mini" icon="iconfont icon-shenqing" @click="alert(scope.row)" />
        </div>
      </el-table-column>
    </el-table>
    <!-- 处理商家 -->
    <el-dialog title="处理商家" :visible.sync="dialogVisible" width="40%" :before-close="handleClose">
      <div v-if="alertshopperform">
        <el-form :model="alertshopperform" label-width="70">
          <el-form-item label="商家">
            <el-input v-model="alertshopperform.u_nickname"></el-input>
          </el-form-item>
          <el-form-item label="状态">
            <el-select v-model="alertshopperform.u_stuta">
              <el-option label="正常" :value="1"></el-option>
              <el-option label="禁用" :value="2"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="handleClose">取 消</el-button>
        <el-button type="primary" @click="submit">修 改</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      num: 0,
      dialogVisible: false,
      shopperlist: [],
      alertshopperform: {}
    }
  },
  created() {
    this.getshopperlist()
  },
  methods: {
    async getshopperlist() {
      const { data: res } = await this.$http.get("showBadGoods", { params: { num: this.num } })
      if (res.code !== 200) return
      this.shopperlist = res.obj
    },
    alert(row) {
      this.dialogVisible = true
      this.alertshopperform = row
      console.log(row)
    },
    async submit() {
      const { u_id, u_stuta } = this.alertshopperform
      const { data: res } = await this.$http.get("updateShopperStatus", { params: { u_id, u_stuta } })
      console.log(res)
      if (res.code !== 200) return
      this.getshopperlist()
      this.handleClose()
    },
    handleClose() {
      this.dialogVisible = false
    }
  },
  watch: {
    num(val) {
      this.getshopperlist()
    }
  }
}
</script>
